import { Component } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireStorage, AngularFireUploadTask } from '@angular/fire/storage';
import { AlertController, IonicPage, LoadingController, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { User } from '../../shared/user';

@IonicPage()
@Component({
  selector: 'page-profiledetail',
  templateUrl: 'profiledetail.html',
})
export class ProfiledetailPage {
  selectedFile : any;
  filepath2 : any;
  emailError : any;
  note: any ;
  img1 : any;
  src : any;
  downloadableURL = '';                      
  task: AngularFireUploadTask;               
userid : any;
  progressValue: Observable<number>; 
  basePath = '/photoAlbum';
  inputFile : any;
  userId : any;
updateDetail : boolean =false;
enableimgupdate : boolean;
  user = {} as User;
  userData : any;

  constructor(private afAuth: AngularFireAuth, private db: AngularFireDatabase, private alertCtrl: AlertController,public storage:AngularFireStorage, public loadingCtrl : LoadingController, public navCtrl: NavController, public navParams: NavParams) {
   this.user = this.navParams.get('data2');
   this.userData = this.user;
if(this.user.photourl !="")
this.enableimgupdate = false;
else
this.enableimgupdate = true;


  }

  ionViewDidLoad() {
    this.afAuth.authState.take(1).subscribe(data => {
        if(data && data.email && data.uid){
          this.userId = data.uid;
         
        }else
  
        alert("you are not authenticated")
  
      })//authstatesddd
  }
  removeFile(){
    this.img1 = "";
    this.selectedFile = "";
    /*
  \
    }
  
  */
   
  }






  toggleUpdateDetails(){
    this.updateDetail = !this.updateDetail;
  }


  chooseFile (event) {
    if (event.target.files[0].size < 303110){
    
        if (event.target.files && event.target.files[0]) {
          this.selectedFile = event.target.files[0];
          let reader = new FileReader();
          reader.onload = (event:any) => {
            this.img1 = event.target.result;
          }
          reader.readAsDataURL(event.target.files[0]);
        }
    
      }else{
    
        let alert = this.alertCtrl.create({
          title: '<font color="green"><b> </b> The image should be less then 301KB </font>',
          cssClass: "alert",
          buttons: [
            {
              text: 'Ok',
              role: 'cancel',
              handler: data => {
                this.selectedFile = "";
              }
            },
           
          ]
        });
        alert.present();
    
    
      this.selectedFile = "";
      }//end of else state,
       
      }

      updatephoto(photourl){
        var loading = this.loadingCtrl.create({
          spinner: 'bubbles',
          dismissOnPageChange: true,
          content: 'removing image in progress <br> Please Wait...'
        });
        loading.present();
        this.storage.storage.refFromURL(photourl).delete().then(async ()=>{
          this.user.photourl = ""
          this.db.object('photoalbum/users/'+this.userId).update(this.user)
          .then(()=>{
            loading.dismiss();
            this.enableimgupdate = true;
             let alert = this.alertCtrl.create({
              title: '<font color="green"><b> </b>Profile Image Removed</font>',
              cssClass: "alert",
              buttons: [
                {
                  text: 'Ok',
                  role: 'cancel',
                  handler: data => {
        
                  }
                },
               
              ]
            });
            alert.present();

          })//end od update



        });//end of delete

      }



      error(){
        let alert = this.alertCtrl.create({
          title: '<font color="green"><b> </b> Full Name, State, Email, LGA, PhoneNo & Address are not optional</font>',
          cssClass: "alert",
          buttons: [
            {
              text: 'Ok',
              role: 'cancel',
              handler: data => {
    
              }
            },
           
          ]
        });
        alert.present();
    
    
    
    
       }//end of error

       async update(user){
   
        var loading = this.loadingCtrl.create({
          spinner: 'bubbles',
          dismissOnPageChange: true,
          content: 'Data updating in progress <br> Please Wait...'
        });
        loading.present();
      
        
        if (this.selectedFile) {
          const filePath = `${this.basePath}/${user.name.trim()+user.lga}`;  
          this.filepath2 = filePath;// inorder to get the path to delete file
          this.task =  this.storage.upload(filePath, this.selectedFile);    
          
          
          this.progressValue = this.task.percentageChanges();       // <<<<< Percentage of uploading is
          (await this.task).ref.getDownloadURL().then(url => {this.downloadableURL = url;
          
          user.photourl = this.downloadableURL;
        
            try {
            
                this.db.object('photoalbum/users/'+this.userId).update(user)
                .then(()=>{
                
        
                  let alert2 = this.alertCtrl.create({
                    
                    title: '<font color="green"><b> </b>Profile update Successful </font>',
                    cssClass: "alert",
                    buttons: [
                      {
                        text: 'Ok',
                        role: 'cancel',
                        handler: data => {
                        }
                      },
                      
                    ]
                  });
                  alert2.present();
                  loading.dismiss();
        
        
          
                    
                    
                  });
        
      
              
        } catch(error){ 
              this.emailError = error.message;
              alert(error.message);
              
            console.error(error);
            
            }
        
        
          
          
          });///end of await  
      
        }else{
          var loading = this.loadingCtrl.create({
            spinner: 'bubbles',
            dismissOnPageChange: true,
            content: 'Data updating in progress <br> Please Wait...'
          });
          loading.present();
         
          try {
            //user.photourl = "";
            this.db.object('photoalbum/users/'+this.userId).update(user)
            .then(()=>{
            
              
              let alert2 = this.alertCtrl.create({
                
                title: '<font color="green"><b> </b>Profile update Successful </font>',
                cssClass: "alert",
                buttons: [
                  {
                    text: 'Ok',
                    role: 'cancel',
                    handler: data => {
                    //  this.afAuth.auth.signOut().then(()=>{})
                    }
                  },
                  
                ]
              });
              alert2.present();
              loading.dismiss();
      
      
      
                
                
              });
      
      
          
      } catch(error){ 
          this.emailError = error.message;
          alert(error.message);
          
        console.error(error);
        
        }
      
        } //end of if file selected methodd
          
        
      
      
      
      }//end of update
      











  










}



