import { Component,    ViewChild } from '@angular/core';
import { Alert, NavController, AlertController, Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
//declare let iosupload : any;
import { User } from '../../shared/user';
import { Observable } from 'rxjs';
import { LoadingController } from 'ionic-angular/components/loading/loading-controller';
import { UserProvider } from '../../providers/user/user';
import { AngularFireStorage, AngularFireUploadTask } from '@angular/fire/storage';
import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireAuth } from '@angular/fire/auth';
import { ProfiledetailPage } from '../profiledetail/profiledetail';




@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
@ViewChild(Nav) nav: Nav;
//@ViewChild('myNav') nav: NavController
  user = {} as User;
  // navParent : NavController= this.nav.;
  

  selectedFile : any;

  filepath2 : any;
  emailError : any;
  note: any ;
  img1 : any;
  src : any;
  downloadableURL = '';                      
  task: AngularFireUploadTask;               
userid : any;
  progressValue: Observable<number>; 
  basePath = '/photoAlbum';
  inputFile : any;
 // element: HTMLElement = document.getElementById('file') as HTMLElement;

  errorMsg: any;

  constructor(private alertCtrl: AlertController, public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen,
    private db: AngularFirestore, private fireStorage: AngularFireStorage,private userservice: UserProvider,public loadingCtrl: LoadingController
    ,private db2: AngularFireDatabase,
    public navCtrl : NavController,
    private afAuth: AngularFireAuth,


    ) {
  /*  
//i contructor
iosupload.onchange = (ev: Event) => {
 alert("hello");
}
*/
  }

  chooseFile (event) {
if (event.target.files[0].size < 303110){

    if (event.target.files && event.target.files[0]) {
      this.selectedFile = event.target.files[0];
      let reader = new FileReader();
      reader.onload = (event:any) => {
        this.img1 = event.target.result;
      }
      reader.readAsDataURL(event.target.files[0]);
    }

  }else{

    let alert = this.alertCtrl.create({
      title: '<font color="green"><b> </b> The image should be less then 301KB </font>',
      cssClass: "alert",
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          handler: data => {
            this.selectedFile = "";
          }
        },
       
      ]
    });
    alert.present();


  this.selectedFile = "";
  }//end of else state,
   
  }





  async uploadFile (user : any) {

  if (this.selectedFile) {
  
  var loading = this.loadingCtrl.create({
    spinner: 'bubbles',
    dismissOnPageChange: true,
    content: 'Data uploading in progress <br> Please Wait...'
  });
  loading.present();
  
  const filePath = `${this.basePath}/${user.name.trim()+user.lga}`;  
  this.filepath2 = filePath;// inorder to get the path to delete file
  this.task =  this.fireStorage.upload(filePath, this.selectedFile);    
  
  
  this.progressValue = this.task.percentageChanges();       // <<<<< Percentage of uploading is
  (await this.task).ref.getDownloadURL().then(url => {this.downloadableURL = url;
   
  user.photourl = this.downloadableURL;

    try {
      this.afAuth.auth.createUserWithEmailAndPassword(user.email, user.phone).then((auth)=>{
        this.db2.object('photoalbum/users/'+auth.user.uid).set(user)
        .then(()=>{
         this.afAuth.auth.signOut().then(()=>{

           let alert2 = this.alertCtrl.create({
             
             title: '<font color="green"><b> </b>Upload Successful Your unique ID = <font color="black">'+auth.user.uid+'</font></font>',
             cssClass: "alert",
             buttons: [
               {
                 text: 'Ok',
                 role: 'cancel',
                 handler: data => {
                 //  this.afAuth.auth.signOut().then(()=>{})
                 }
               },
              
             ]
           });
           alert2.present();
           loading.dismiss();


            });
             
            
           });
 
         },error => {
          alert(error);
          loading.dismiss();
          });//end of this.auth
        //end db.authstarte
     //  this.itemsRef.push({ name: user.name, email : user.email, admno: user.admno, department: user.department });
     //  this.afAuth.auth.signInWithEmailAndPassword(user.email, user.password);
    //   var  userg = firebase.auth().currentUser;
      // userg.updateProfile({
        // displayName: "Shanwili",
         //photoURL: "klslsls"
      // });
    
       
 } catch(error){ 
      this.emailError = error.message;
      alert(error.message);
      
     console.error(error);
     
    }
 
 
  //end of register 
 









  
  
  
  
  
  
  
  });///end of await  
  
  
  
  
  
  
  
  
     } else { 
       

      let alert1 = this.alertCtrl.create({
        title: '<font color="green"><b></b>No image selected, upload without image</font>',
        cssClass: "alert",
        buttons: [
          {
            text: 'NO',
            role: 'cancel',
            handler: data => {
  
            }
          },
          {
            text: 'Yes Continue',
            handler: data => {
              var loading = this.loadingCtrl.create({
                spinner: 'bubbles',
                dismissOnPageChange: true,
                content: 'Uploading data is in progress <br> Please Wait...'
              });
             loading.present();
             user.photourl = "";
             try {
     
     this.afAuth.auth.createUserWithEmailAndPassword(user.email, user.phone).then((auth)=>{
            this.db2.object('photoalbum/users/'+auth.user.uid).set(user)
            .then(()=>{
             this.afAuth.auth.signOut().then(()=>{
 
               let alert2 = this.alertCtrl.create({
                 
                 title: '<font color="green"><b> </b>Upload Successful Your unique ID = <font color="black">'+auth.user.uid+'</font></font>',
                 cssClass: "alert",
                 buttons: [
                   {
                     text: 'Ok',
                     role: 'cancel',
                     handler: data => {
                     //  this.afAuth.auth.signOut().then(()=>{})
                     }
                   },
                  
                 ]
               });
               alert2.present();
               loading.dismiss();
           
   
              
   
 
 
             });
              
             
            });
  
          },error => {
            alert(error);
            loading.dismiss();
            });//end of this.auth
          
                  //end db.authstarte
              //  this.itemsRef.push({ name: user.name, email : user.email, admno: user.admno, department: user.department });
              //  this.afAuth.auth.signInWithEmailAndPassword(user.email, user.password);
             //   var  userg = firebase.auth().currentUser;
              
             
                
          } catch(error){ 
            loading.dismiss();
               this.emailError = error.message;
               alert(error.message);
               
              
             }
          
          
           //end of register 
          










             
              
            
  
  
              }
          }
        ]
      });
      alert1.present();
  





      
      
    //   alert('No images ected');
   //    loading.present();
     
   
 
    }//end of else
   }//end of method
  



   error(){
    let alert = this.alertCtrl.create({
      title: '<font color="green"><b> </b> Full Name, State, Email, LGA, PhoneNo & Address are not optional  </font>',
      cssClass: "alert",
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          handler: data => {

          }
        },
       
      ]
    });
    alert.present();




   }//end of error

  
  removeFile(){
    this.img1 = "";
    this.selectedFile = "";
    /*
  \
    }
  
  */
   
  }




  presentPrompt() {
    let alert1 = this.alertCtrl.create({
      title: 'Login',
      cssClass: 'alertb',
      inputs: [
        {
          name: 'username',
          placeholder: 'Your Email...'
        },
        {
          name: 'password',
          placeholder: 'Your Phone No...',
          type: 'password'
        }
      ],
      buttons: [
        {
          text: 'Go back',
          role: 'cancel',
          handler: data => {
           
          }
        },
        {
          text: 'Login',
          handler: data => {
            let loading = this.loadingCtrl.create({
              spinner: 'bubbles',
              dismissOnPageChange: true,
              content: 'Logging Please Wait...'
            });
          
            loading.present();
          
          
            
           
           try{
                this.afAuth.auth.signInWithEmailAndPassword(data.username, data.password).then(()=>{           
                 
                  this.afAuth.authState.take(1).subscribe(data =>{
                    if(data && data.email && data.uid){
                        let uid = data.uid;
                          
                                  this.db2.object('photoalbum/users/'+data.uid).valueChanges().subscribe(
                                    data2 => {
                                      this.navCtrl.push(ProfiledetailPage,{
                                        data2: data2
                                      });
                                      
                                      loading.dismiss();
                                   //   data2.indexOf(passNo) !== -1 ?  this.storage.set(_id, true) : alert("you cannot start this module becouse You dint passsed the previos module");
                      
                                    },error => {
                                      alert(error);
                                      loading.dismiss();
                                      });
                        
                  // place to add something if we need update
                      
                    }else{
                      loading.dismiss();
                      this.presentPrompt();}
               });//end of this.auth

                 
                 
            //   this.nav.push(ContactPage);
               
               
 
                },error => {
                  alert(error);
                  loading.dismiss();
                  });//end of this.auth
                
               
          
              }catch(error){
                console.error(error);
                this.errorMsg = error.message;
              
                loading.dismiss();

              }//end of try catch
           





          }//end of data
        }
      ]
    });
    alert1.present();
  }

 
push(){
  this.navCtrl.push(ProfiledetailPage);
}






}



