import { Component,    ViewChild } from '@angular/core';
import { Alert, NavController, AlertController, Nav, Platform } from 'ionic-angular';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { HomePage } from '../pages/home/home';
import { User } from './../shared/user';
import { Observable } from 'rxjs';
import { LoadingController } from 'ionic-angular/components/loading/loading-controller';
import { UserProvider } from '../providers/user/user';
import { AngularFireStorage, AngularFireUploadTask } from '@angular/fire/storage';
import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireAuth } from '@angular/fire/auth';
import { ProfiledetailPage } from '../pages/profiledetail/profiledetail';
import {App} from 'ionic-angular';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
@ViewChild('myNav') nav: NavController
//@ViewChild(Nav) nav: Nav;
navg : NavController;
  user = {} as User;
  // navParent : NavController= this.nav.;



rootPage: any = HomePage;
  

  pages: Array<{title: string, icon: string, component: any}>;
  errorMsg: any;

  constructor(private alertCtrl: AlertController, public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen,

    ) {
    this.initializeApp();

    // used for an example of ngFor and navigation  
    

  }

  initializeApp() {
    this.platform.ready().then(() => {
     
      this.statusBar.styleDefault();
      this.splashScreen.hide();
      
   
    });
  }


}



